	initDistanceGrid() {
		// only init grid if it does not yet exist
		if ( !this.distances_grid ) {
			var grid_parent_container = this.edit_view_tab.find( '#tab_map_distances_content_div' );
			this.distances_grid = this.buildDistancesGrid( 'distance_grid_table' );
			grid_parent_container.append( this.distances_grid );
		}
	}

	buildDistancesGrid( grid_selector ) {
		var column_info_array = [];
		var column_info_default = {
			name: 'default',
			index: 'default',
			label: $.i18n._( 'Default' ),
			// width: 100,
			sortable: true,
			formatter: 'select',
			editable: false,
			title: false,
			edittype: 'select'
		};

		function _addColumn2Grid( field, label, options ) {
			var column_info = {
				name: field,
				index: field,
				label: label // do not put the $.i18n reference directly in the function, as it will interfere with the extraction of language labels for translation files
			};
			if ( options ) {
				jQuery.extend( column_info, options ); // have the flexibility here to add individual column options now that we refactored to a function
			}
			column_info_array.push( jQuery.extend( {}, column_info_default, column_info ) );
		}

		_addColumn2Grid( 'first_name', $.i18n._( 'First Name' ) );
		_addColumn2Grid( 'last_name', $.i18n._( 'Last Name' ) );
		_addColumn2Grid( 'branch', $.i18n._( 'Branch' ) );
		_addColumn2Grid( 'department', $.i18n._( 'Department' ) );
		_addColumn2Grid( 'job_manual_id', $.i18n._( 'Job Code' ) );
		_addColumn2Grid( 'job', $.i18n._( 'Job' ) );
		_addColumn2Grid( 'job_item_manual_id', $.i18n._( 'Task Code' ) );
		_addColumn2Grid( 'job_item', $.i18n._( 'Task' ) );
		_addColumn2Grid( 'in_time_stamp', $.i18n._( 'In Punch' ) );
		_addColumn2Grid( 'out_time_stamp', $.i18n._( 'Out Punch' ) );
		_addColumn2Grid( 'total_time', $.i18n._( 'Total Time' ) );
		_addColumn2Grid( 'duration', $.i18n._( 'Driving Duration' ) );
		_addColumn2Grid( 'distance', $.i18n._( 'Driving Distance' ) );
		_addColumn2Grid( 'in_latitude', $.i18n._( 'In Latitude' ) );
		_addColumn2Grid( 'in_longitude', $.i18n._( 'In Longitude' ) );
		_addColumn2Grid( 'out_latitude', $.i18n._( 'Out Latitude' ) );
		_addColumn2Grid( 'out_longitude', $.i18n._( 'Out Longitude' ) );

		// Future option. If implementing, create the function below based off showEmployeeSettingNoResultCover()
		// if ( data.length < 1 ) {
		// 	this.showDistanceTableNoResultCover();
		// }

		var this_MapViewController = this; // Reference local scope for callbacks to reference further down in the function. Var name also identifies what 'this' is referring to.
		var data = this.getDistancesGridData();

		return new TTGrid( grid_selector, {
			data: data,
			gridComplete: function() {
				this_MapViewController.setDistancesTableGridSize();
			},
			multiselect: false
		}, column_info_array );
	}

	getDistancesGridData() {
		var this_MapViewController = this; // Reference local scope for callbacks to reference further down in the function. Var name also identifies what 'this' is referring to.
		var grid_source = [];
		var route_array = this.incoming_data.tt_map_data.tt_routes;

		if ( !route_array || route_array.length < 1 ) {
			// error? there are no calculated routes. No need to error for now, as its likely this will be handled earlier.
			return false;
		}

		Object.values( route_array ).map( function( route ) {
			var in_punch = route.route_markers[0].punch;
			var out_punch = route.route_markers[1].punch;

			if ( in_punch.status !== 'In'
				|| out_punch.status !== 'Out' ) {
				Debug.Text( 'ERROR: in_punch and out_punch have data mismatch punch In/Out status', 'MapViewController.js', 'MapViewController', 'getDistancesGridData', 1 );
			}

			// For some of the data we will only refer to in_punch. Therefore, make sure the data matches.
			if ( in_punch.first_name !== out_punch.first_name
				|| in_punch.last_name !== out_punch.last_name
				|| in_punch.branch !== out_punch.branch
				|| in_punch.department !== out_punch.department
				|| in_punch.job !== out_punch.job
				|| in_punch.job_item !== out_punch.job_item
			) {
				Debug.Text( 'ERROR: in_punch and out_punch have data mismatch.', 'MapViewController.js', 'MapViewController', 'getDistancesGridData', 1 );
			}

			var table_filters = [false, undefined, null, 'false', 'undefined', 'null'];
			var table_entry = {
				first_name: Global.filterOutput( in_punch.first_name, table_filters ),
				last_name: Global.filterOutput( in_punch.last_name, table_filters ),
				in_time_stamp: Global.filterOutput( in_punch.time_stamp, table_filters ),
				in_latitude: Global.filterOutput( in_punch.latitude, table_filters ),
				in_longitude: Global.filterOutput( in_punch.longitude, table_filters ),
				out_time_stamp: Global.filterOutput( out_punch.time_stamp, table_filters ),
				out_latitude: Global.filterOutput( out_punch.latitude, table_filters ),
				out_longitude: Global.filterOutput( out_punch.longitude, table_filters ),
				distance: '',
				duration: '',
				branch: Global.filterOutput( in_punch.branch, table_filters ),
				department: Global.filterOutput( in_punch.department, table_filters ),
				job_manual_id: Global.filterOutput( in_punch.job_manual_id, table_filters ),
				job: Global.filterOutput( in_punch.job, table_filters ),
				job_item_manual_id: Global.filterOutput( in_punch.job_item_manual_id, table_filters ),
				job_item: Global.filterOutput( in_punch.job_item, table_filters ),
				total_time: Global.filterOutput( Global.getTimeUnit( in_punch.total_time ), table_filters )
			};
			var row_route_info;
			// if route info exists, set it, else try to acquire it from the main routes
			if ( route.route_distance && route.route_duration ) {
				row_route_info = {
					distance: TTConvertMapData.convertMetersToUserUnits( route.route_distance ).distance_in_user_format,
					duration: Global.getTimeUnit( route.route_duration )
				};
				jQuery.extend( table_entry, row_route_info );

				// check for duplicate route flag, if there is one, look-up the reference for that duplicate route, and request the route info.
			} else if ( route.foundDuplicateLine && this_MapViewController.calculatedRouteData[route.ref_id] ) {
				var existing_route_info = this_MapViewController.calculatedRouteData[route.ref_id];

				row_route_info = {
					distance: TTConvertMapData.convertMetersToUserUnits( existing_route_info.route_distance ).distance_in_user_format,
					duration: Global.getTimeUnit( existing_route_info.route_duration )
				};
				jQuery.extend( table_entry, row_route_info );
			} else {
				Debug.Text( 'Note: No distance data found for entry. Leaving as empty strings', 'MapViewController.js', 'MapViewController', 'getDistancesGridData', 10 );
			}
			grid_source.push( table_entry );
		} );

		return grid_source;
	}

	setDistancesTableGridSize( grid ) {
		if ( !grid ) {
			return;
		}

		var tab_distances_table = this.edit_view.find( '#tab_map_distances_content_div' );
		grid.grid.setGridWidth( tab_distances_table.width() );
		grid.grid.setGridHeight( tab_distances_table.height() );
		grid.setGridColumnsWidth();
	}

